<template>
	<div id="resumeEditor">
		<nav>
			<ol>
				<li v-for="(item,index) in resume.config" :class="{active: item.field === selected}" @click="fieldChange(item.field)">
					<svg class="icon">
						<use :xlink:href="`#icon-${item.icon}`"></use>	
					</svg>
				</li>
			</ol>
		</nav>
		<ol class="panels">
			<li v-for="item in resume.config" v-show="item.field === selected">
				<div v-if="resume[item.field] instanceof Array">
					<div class="subitem" v-for="(subitem,index) in resume[item.field]">
						<div class="resumeField" v-for="(value,key) in subitem">
							<label>{{key}}</label>
							<input type="text" :value="value" @input="setDatas({'field':item.field,index,key,value:$event.target.value})">
						</div>
					</div>
				</div>
				<div v-else class="resumeField" v-for="(value,key) in resume[item.field]">
					<label>{{key}}</label>
					<input type="text" :value="value" @input="setData({'field':item.field,key,value:$event.target.value})">
				</div>
			</li>

		</ol>		
	</div>
</template>

<script>
import {mapState, mapMutations} from 'vuex'
	export default {
		name: 'ResumeEditor',
		computed: {
			...mapState([
				'selected','resume'
			])
		},
		methods: {
			...mapMutations([
				'fieldChange','setData','setDatas'
			])
		}
	}
</script>

<style lang="stylus">
#resumeEditor
	background #fff;
	box-shadow 0 1px 3px 0 rgba(0,0,0,0,25);
	display flex;
	flex-direction row;
	nav 
		width 80px;
		background black;
		color: #ffffff;
		li 
			height 48px;
			display flex;
			justify-content center;
			align-items center;
			margin-top 16px;
			margin-bottom 16px;
		.active
			background: white;
			color #000;
		svg.icon
			width 24px;
			height 24px;
	.panels
		flex-grow 1;
		li
			padding 25px
			.resumeField 
				label
					display block;

			 	input[type=text]
					margin 16px 0;
					border 1px solid #ddd;
					box-shadow inset 0 1px 3px 0 rgba(0,0,0,0.25);
					width: 100%;
					height 40px;
					padding 0 8px;
			.subitem::after
				content: '';
				display block;
				width 100%;
				border-bottom 1px solid #ccc;
				margin: 20px 0;
</style>


