<template>

  <div class="dialog-wrapper" v-show="visible">
      <div class="dialog">
          <header>
              {{title}}
              <span class="close" @click="close">X</span>
          </header>
          <main>
            <div class="signUp" v-show="signUpVisible">
               <form @submit.prevent="signUp">
                   <div class="row">
                       <label>用户名</label>
                       <input type="text" v-model="formData.username">
                   </div>
                   <div class="row">
                       <label>密码</label>
                       <input type="password" v-model="formData.password">
                   </div>
                   <div class="actions">
                       <input type="submit" value="注册">
                   </div>
               </form>
            </div>
            <div class="signIn" v-show="signInVisible"> 
               <form @submit.prevent="signIn">
                   <div class="row">
                       <label>用户名</label>
                       <input type="text" v-model="formData.username">
                   </div>
                   <div class="row">
                       <label>密码</label>
                       <input type="password" v-model="formData.password">
                   </div>
                   <div class="actions">
                       <input type="submit" value="登录">
                   </div>
               </form>
            </div>
          </main>
      </div>
  </div>
</template>

<script>

import AV from './leancloud'
export default {
  name: 'MyDialog',
  data(){
      return {
          formData: {
              username: '',
              password: ''
          }
      }
  },
  props: ['title','visible','signUpVisible','signInVisible'],
  methods: {
        close(){
            this.$emit('close')
        },
        signUp(){
            console.log('submit')
            let {username,password} = this.formData;
            var user = new AV.User();
            user.setUsername(username);
            user.setPassword(password);
            user.signUp().then((loginedUser)=>{
                console.log('signUp')
                this.$emit('success',{
                    username: loginedUser.attributes.username,
                    id: loginedUser.id
                })
            }
            )
        },
        signIn(){
            console.log('signin')
            let {username,password} = this.formData;
            AV.User.logIn(username,password).then((loginedUser)=>{
                this.$emit('success',{
                username: loginedUser.attributes.username,
                id: loginedUser.id
                })
            })
        }
    }
}
</script>

<style scoped lang="stylus">
.dialog-wrapper
    position fixed;
    background rgba(0,0,0,0.7);
    top 0;
    left 0;
    bottom 0;
    right 0;
    margin 0;
    display flex;
    justify-content center;
    align-items center;
    .dialog
        background white;
        min-height 4em;
        min-width 10em;
        header 
            padding 16px;
            display flex;
            justify-content space-between;
        main 
            padding 16px;

</style>



