<template>
	<div id="resumePreview">
		<section data-name="profile" v-show="resume.profile">
			<h1>
				{{resume.profile.name}}
			</h1>
			<h2>{{resume.profile.title}}</h2>
			<p>
				<small>{{resume.profile.city}}</small>
				<small>{{resume.profile.birthday}}</small>
			</p>
		</section>
		<section data-name="work" v-show="resume.work">
			<h2>工作经历</h2>
			<ol>
				<li v-for="item in resume.work">
					<h3>{{item.company}}</h3>
					<p v-show="item.content">{{item.content}}</p>
				</li>
			</ol>
		</section>

		<section data-name="education" v-show="resume.education">
			<h2>教育情况</h2>
			<ol>
				<li v-for="item in resume.education">
					<h3>{{item.school}}</h3>
					<p v-show="item.content">{{item.content}}</p>
				</li>
			</ol>
		</section>

		<section data-name="projects" v-show="resume.projects">
			<h2>项目经历</h2>
			<ol>
				<li v-for="item in resume.projects">
					<h3>{{item.name}}</h3>
					<p v-show="item.content">{{item.content}}</p>
				</li>
			</ol>
		</section>

		<section data-name="awards" v-show="resume.awards">
			<h2>获奖情况</h2>
			<ol>
				<li v-for="item in resume.awards">
					<h3>{{item.name}}</h3>
					<p v-show="item.content">{{item.content}}</p>
				</li>
			</ol>
		</section>

		<section data-name="contacts" v-show="resume.contacts">
			<h2>联系方式</h2>
			<ol>
				<li v-for="item in resume.contacts">
					{{item.contact}}  {{item.content}}
				</li>
			</ol>
		</section>
	</div>
</template>

<script>
import {mapState} from 'vuex'
	export default {
		name: 'ResumePreview',
		computed: {
			...mapState([
				'selected','resume'
			])
		}
	}
</script>

<style lang="stylus">
* 
	font-weight normal;
	box-sizing border-box;
#resumePreview
	box-shadow 0 1px 3px 0 rgba(0,0,0,0,25);
	background #fff;
	color #333;
	line-height 1.2;
	padding 2em;
	overflow auto;
	section+section
		margin-top 2em;
	section 
		h2:first-child
			background #ddd;
			display inline-block;
			padding .2em;
			margin-bottom .5em;
		p
			white-space pre-line;
	section[data-name="profile"]
		h1
			margin .1em 0;
			font-size: 4em;
	section[data-name="work"],section[data-name="projects"],section[data-name="awards"]
		li+li
			margin-top: 1em;
		li
			h3 
				border-bottom 1px solid #999;
				padding-bottom .3em;
				margin-bottom .3em;
	section[data-name="education"]
		li 
			line-height 1.5
</style>

</style>


