import Vuex, { Store } from 'vuex'
import Vue from 'vue'
import AV from '../components/leancloud'

Vue.use(Vuex)
export default new Vuex.Store({
    state: {
        selected: 'profile',
        user: {
            id: '',
            username: ''
        },
        resume: {
            config: [
                { field: 'profile', icon: 'id' },
                { field: 'work', icon: 'work' },
                { field: 'education', icon: 'book' },
                { field: 'projects', icon: 'heart' },
                { field: 'awards', icon: 'cup' },
                { field: 'contacts', icon: 'phone' },
            ],
            profile: {
                name: '',
                city: '',
                title: '',
                birthday: ''
            },
            work: [
                {company: 'AL', content: '我的第二份工作是'},
                {company: 'TX', content: '我的第一份工作是'},
            ],
            education: [
                {school: 'yizhong',content: ''},
                {school: 'scut', content: ''}
            ],
            projects: [
                {name: 'project A',conmtent:'文字'},
                {name: 'project B',conmtent:'文字'}
            ],
            awards: [
                {name: 'award A',conmtent:'文字'},
                {name: 'award B',conmtent:'文字'}
            ],
            contacts: [
                {contact: 'phone',content:'110'},
                {contact: 'qq',content:'119'}
            ]
        }
    },
    mutations: {
        fieldChange(state,field){
            state.selected = field
        },
        setData(state,{field,key,value}){
            state.resume[field][key] = value;
            localStorage.setItem('state',JSON.stringify(state));
        },
        setDatas(state,{field,index,key,value}){
            state.resume[field][index][key] = value;
            localStorage.setItem('state',JSON.stringify(state));
        },
        initState(state,payload){
            console.log(payload)
            state.selected = payload.selected;
            state.resume = payload.resume;
        },
        setUser(state,user){
            state.user = user;
            console.log(state.user)
        },
        removeUser(state){
            state.user.id = null;
            state.id = null;
        },
        saveResume(state){
            let dataString = JSON.stringify(state);
            let AVData = AV.Object.extend('Resume');
            var avData = new AVData();
            avData.set('content',dataString);

            var acl = new AV.ACL();
            acl.setReadAccess(AV.User.current(),true);
            acl.setWriteAccess(AV.User.current(),true);

            avData.setACL(acl);
            avData.save().then((data)=>{
                state.id = data.id
                console.log(state.id)
            });
        },
        updateResume(state){
            let dataString = JSON.stringify(state);
            let avData = AV.Object.createWithoutData('Resume',state.id);
            avData.set('content',dataString);
            avData.save()
            console.log('update')
        },
        fetchResume(state){
            if(state.user.id){
                let query = new AV.Query('Resume');
                query.find().then((resume)=>{
                    let resumes = resume[0];
                    if(resume[0]){
                        console.log('???')
                        let id = resumes.id;
                        let data = JSON.parse(resumes.attributes.content);
                        state.resume = data.resume;
                        state.id = id;  
                        console.log('error')
                    }else{
                        console.log('无数据')
                    }
                })
            }
        }
    }
})


