<template>
	<div id="topbar">
		<div class="wrapper">
			<span class="log">Resumer</span>
			<div class="actions">
				<div v-if="logined" class="userActions">
					<span>你好,{{user.username}}</span>
					<a class="button" @click.prevent="signOut">登出</a>
					<button class="button primary" @click="save">保存</button>
				</div>
				<div v-else class="userActions">
					<a class="button primary" href="#" @click.prevent="signUpVisible = true">注册</a>
					<MyDialog :visible = "dialogVisible" :signInVisible="signInVisible" :signUpVisible="signUpVisible" @close="reset()" @success="signIn($event)">
					</MyDialog>
					<a href="#" class="button" @click.prevent = "signInVisible = true">登录</a>
					<button class="button primary" @click="save">保存</button>
					<button class="button">预览</button>
				</div>

			</div>
		</div>
	</div>
</template>

<script>
import MyDialog from './Mydialog'
import AV from './leancloud'
	export default {
		name: 'Topbar',
		data(){
			return {
				signInVisible: false,
				signUpVisible: false
			}
		},
		computed: {
			user(){
				return this.$store.state.user
			},
			logined(){
				return this.user.id
			},
			dialogVisible(){
				return (this.signInVisible || this.signUpVisible);
			}
		},
		components: {
			MyDialog
		},
		methods: {
			signIn(user){
				this.reset();
				this.$store.commit('setUser',user);
				this.$store.commit('fetchResume');
			},
			signOut(){
				AV.User.logOut();
				this.$store.commit('removeUser')
			},
			reset(){
				this.signInVisible = false;
				this.signUpVisible = false;
			},
			save(){
				if(this.$store.state.user.id){
					if(this.$store.state.id){
						this.$store.commit('updateResume');
					}else{
						this.$store.commit('saveResume')
					}
				}
			}
		}
	}
</script>

<style scoped lang="stylus">
#topbar
	background: white;
	box-shadow: 0 1px 3px 0 rgba(0,0,0,0.25);
	height: 64px;
	padding: 0 16px;
	.wrapper
		background #fff;
		min-width: 1024px;
		max-width: 1440px;
		margin 0 auto;
		height 64px;
		display flex;
		justify-content space-between;
		align-items center;
		padding 0 16px;
		.logo
			font-size 24px;
			color #000;
		.button
			width 72px;
			height 32px;
			border none;
			cursor pointer;
			font-size 18px;
			background #ddd;
			color #222;
			text-decoration none;
			display: inline-flex;
			justify-content center;
			align-items center;
			vertical-align middle;
			&:hover
				box-shadow 1px 1px 1px rgba(0, 0, 0, 0.50);
			&.primary
				background #02af5f;
				color white;
</style>


